import React, { useState } from 'react'
import styled from "styled-components";
import StarRatings from 'react-star-ratings';
import comments from '../../../../../../../assets/global/goods/comments/comment-icon.svg'
const Div = styled.div`
    display: flex;
    margin-bottom: 18px;
`
const Stars = styled.div`
    position: relative;
    margin-right: 13px;
`

const Comments = styled.div`
    display: flex;
    align-items: center;
    img{
        display: block;
        margin-right: 7px;
    }
`
const Count = styled.div`
    position: relative;
    color: #D0D0D0;
    :after{
        content: ')';
    }
    :before{
        content: '('
    }
`

const Rating = (props) => {
    const [rating, newRating] = useState(() => 5)
    return(
        <Div>
            <Stars>
                <StarRatings
                    rating={rating} 
                    numberOfStars={5}
                    changeRating={ () => {newRating()} }
                    starRatedColor='#FFCC01'
                    starEmptyColor='#D0D0D0'
                    starHoverColor='#FFCC01'
                    starDimension='26px'
                    starSpacing='0px'
                    name='product-rating'
                />
            </Stars>
            <Comments>
                <img src={comments} alt="Комментарии" />
                <Count> {props.comments} </Count>
            </Comments>

        </Div>
    )
}

export default Rating
