import React from 'react'
import styled from "styled-components";

const Div = styled.div`
    font-style: normal;
    font-weight: bold;
    font-size: 30px;
    line-height: 35px;
    text-transform: uppercase;

    color: #333333;

    margin-bottom: 45px;
`

const Title = (props) => {
    return (
        <Div>
            {props.title}
        </Div>
    )
}

export default Title
