import React from 'react'
import styled from 'styled-components'
import { QuestionCircleOutlined } from '@ant-design/icons';
import { Collapse } from 'antd';
const { Panel } = Collapse;

const Div = styled.div`
   
`
const Items = styled.div`
   
`
const Item = styled.div`
   display: flex;
   align-items: center;
   :not(:last-child){
       margin-bottom: 16px;
   }
`
const Img = styled.div`
   margin-right: 16px;
`
const Text = styled.div`
   font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 116%;

    color: #888888;
`


const Info = () => {
    return (
        <Div>
            <Collapse bordered={false} defaultActiveKey={['1']}>
                <Panel header='ДОСТАВКА' key="1">
                    <Items>
                        <Item>
                            <Img><QuestionCircleOutlined /></Img>
                            <Text>Бесплатная доставка при заказе от 3000 грн</Text>
                        </Item>
                        <Item>
                            <Img><QuestionCircleOutlined /></Img>
                            <Text>Доставка Новой Почтой</Text>
                        </Item>
                        <Item>
                            <Img><QuestionCircleOutlined /></Img>
                            <Text>Доставка Укрпочтой</Text>
                        </Item>
                        <Item>
                            <Img><QuestionCircleOutlined /></Img>
                            <Text>Курьерская адресная доставка</Text>
                        </Item>
                        
                    </Items>
                </Panel>
                <Panel header="ОПЛАТА" key="2">
                    <Items>
                        <Item>
                            <Img><QuestionCircleOutlined /></Img>
                            <Text>При получении (наложеный платеж)</Text>
                        </Item>
                        <Item>
                            <Img><QuestionCircleOutlined /></Img>
                            <Text>Оплату на карту (без комиссии)</Text>
                        </Item>     
                    </Items>
                </Panel>
                <Panel header="ГАРАНТИЯ" key="3">
                <Items>
                        <Item>
                            <Img><QuestionCircleOutlined /></Img>
                            <Text>При получении (наложеный платеж)</Text>
                        </Item>
                        <Item>
                            <Img><QuestionCircleOutlined /></Img>
                            <Text>Оплату на карту (без комиссии)</Text>
                        </Item>                   
                    </Items>
                </Panel>
            </Collapse>
        </Div>
    )
}

export default Info