import React, { useState } from 'react'
import styled from 'styled-components'
import 'antd/dist/antd.css';
import { Slider, InputNumber } from "antd";
import { removeAllSpaces } from '../../../../../../functions';

const Div = styled.div`

`
const Row = styled.div`
    display: flex;
    input:first-child{
            border: 1px solid red;
        }
`

const Cost = (props) => {
    const [minValue, setMinValue] = useState(0)
    const [maxValue, setMaxValue] = useState(10)

    const minCostLimit = props.goods.reduce((minCost = removeAllSpaces(props.goods[0].currentCost), el) => {
        const cost = removeAllSpaces(el.currentCost)

        if(minCost > cost){
            minCost = cost
        }
        
        return minCost
    })
    const maxCostLimit = props.goods.reduce((maxCost = removeAllSpaces(props.goods[0].currentCost), el) => {
        const cost = removeAllSpaces(el.currentCost)
        
        if(maxCost > cost){
            maxCost = cost
        }
        
        return maxCost
    })
    
    const getMinInputValue = (e) => {
        const value = e.target.value
        setMinValue(value)
    }
    const getMaxInputValue = (e) => {
        const value = e.target.value
        setMaxValue(value)
    }
    return(
        <Div>
            <Row>
                <InputNumber
                    min={minCostLimit}
                    max={maxCostLimit}
                    style={{ margin: '0 16px', position: 'relative' }}
                    value={minCostLimit}
                    onChange={getMinInputValue}
                />
                <InputNumber
                    min={minCostLimit}
                    max={maxCostLimit}
                    style={{ margin: '0 16px' }}
                    value={maxCostLimit}
                    onChange={getMaxInputValue}
                />
            </Row>
                <Slider 
                    min={minCostLimit}
                    max={maxCostLimit}
                    onChange={[setMinValue, setMaxValue]}
                    range={{ draggableTrack: true }} 
                    defaultValue={[minValue, maxValue]} 
                />
        </Div>




    )
}

export default Cost