import React from 'react'
import styled from 'styled-components'
import { itemColorActive, itemColorUnactive } from '../../../../../../../../functions'
const Text = styled.div`
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 116%;

    color: #888888;
    margin-bottom: 10px;
    
    :last-child{
        margin-bottom: 30px;
    }
`

const Item = (props) => {

    return (
        <Text onMouseOut={itemColorUnactive} onMouseOver={itemColorActive}>
            { props.name }
        </Text>
    )
}

export default Item


