import React from 'react'
import styled from 'styled-components'

const Div = styled.div`
    padding: 31px 42px 30px;
    background: #FBD864;
    @media(max-width: 1198px){
        margin-bottom: 40px;
    }
    @media(max-width: 525px){
        padding: 21px 32px 20px;
    }
`
const Form = styled.form`
    display: flex;
    flex-direction: column;
    align-items: center;
`
const Title = styled.div`
    align-self: start;
    font-style: normal;
    font-weight: bold;
    font-size: 26px;
    line-height: 116%;

    color: #333333;
    margin-bottom: 32px;
`
const Row = styled.div`
    width: 100%;
    margin-bottom: 15px;
    display: flex;
    gap: 15px;
    justify-content: space-between;
    @media(max-width: 768px){
        flex-wrap: wrap;
        gap: none;
    }
`
const Input = styled.input`
    border: 0;
    border-radius: 5px;
    padding: 12px 14px;
    flex: 1 1 auto;
    @media(max-width: 768px){
        flex: 1 1 100%;
    }
`
const Textarea= styled.textarea`
    border: 0;
    border-radius: 5px;
    margin-bottom: 25px;
    padding: 12px 14px;
    resize: none;
    width: 100%;
`
const Button = styled.button`
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 16px;
    text-transform: uppercase;

    color: #fff;
    background: #666;
    padding: 15px 32px;
    border: 0;
    border-radius:5px;
`



const Feedback = (props) => {
    return (
        <Div>
            <Form action="#">
                <Title>Есть вопросы или предложения? Напишите нам!</Title>
                <Row>
                    <Input type="text" placeholder='Выше имя:'/>
                    <Input type="email" placeholder='Вас email:'/>
                    <Input type="number" placeholder='Номер телефона:'/>
                </Row>
                <Textarea rows='5' placeholder='Ваш комментарий'></Textarea>
                <Button>отправить</Button>
            </Form>
        </Div>
    )
}

export default Feedback