import React from 'react'
import Body from './Body/Body'
import styled from 'styled-components'

const Div = styled.div`
    background: #333333;
    padding: 60px 0;
    position: relative;
    z-index: -1;
`
const Footer = () => {
    return (
        <Div>
            <Body/>
        </Div>
    )
}

export default Footer