import React from 'react'
import styled from 'styled-components'
import 'antd/dist/antd.css';
import { Collapse } from 'antd';
import Popular from './Popular/Popular'
import Selected from './Selected/Selected'
import Cost from './Cost/Cost';
import ListItems from './ListItems/ListItems';
const { Panel } = Collapse;

const CategoryForm = styled.form`
    padding: 21px 0 27px;
    
    .ant-collapse-item{
        font-style: normal;
        font-weight: bold;
        font-size: 16px;
        line-height: 116%;
        color: #333333;
    }
    .ant-collapse-icon-position-right > .ant-collapse-item > .ant-collapse-header .ant-collapse-arrow{
        transform: translateY(-50%) rotate(-90deg);
    }
    .ant-collapse-item-active > .ant-collapse-header .ant-collapse-arrow svg{
        transform: rotate(180deg)!important;
    }
    .ant-collapse-borderless > .ant-collapse-item:last-child,
    .ant-collapse > .ant-collapse-item:last-child{
        border: 0;
    }
`
const Title = styled.div`
    font-style: normal;
    font-weight: bold;
    font-size: 24px;
    line-height: 28px;

    color: #333333;
    padding: 0 23px;
    margin-bottom: 11px;
`

const Form = (props) => {

    return(
        <CategoryForm>
            <Title>Фильтры</Title>
            <Selected/>
            <Collapse bordered={false} expandIconPosition={'right'}  defaultActiveKey={['1']}>
                <Panel header="Популярное в категории" key="1"> <Popular list = {props.form.popular}/> </Panel>
                <Panel header="Цена, грн." key="2"> <Cost goods={props.goods}/> </Panel>
                <Panel header="Тип велосипеда" key="3"> <ListItems list={props.form.popular} inputName={'type'}/>  </Panel>
                <Panel header="Размер рамы" key="4"> <ListItems list={props.form.popular} inputName={'size'}/>  </Panel>
                <Panel header="Диаметр колеса" key="5"> <ListItems list={props.form.popular} inputName={'wheelDiameter'}/>  </Panel>
                <Panel header="Материал рамы" key="6"> <ListItems list={props.form.popular} inputName={'frameMaterial'}/>  </Panel>
                <Panel header="Тип тормозов" key="7"> <ListItems list={props.form.popular} inputName={'brakeType'}/>  </Panel>
                <Panel header="Год" key="8"> <ListItems list={props.form.popular} inputName={'year'}/>  </Panel>
                <Panel header="Бренд" key="9"> <ListItems list={props.form.popular} inputName={'brand'}/>  </Panel>
                <Panel header="Тип амортизации" key="10"> <ListItems list={props.form.popular} inputName={'depreciationType'}/> </Panel>
            </Collapse>
        </CategoryForm>
    )
}

export default Form