import React from 'react'
import styled from "styled-components";
import { Breadcrumb } from 'antd';
import BreadcrumbItem from './BreadcrumbItem/BreadcrumbItem';

const Div = styled.nav`
    margin-bottom: 54px;
`

const BreadcrumbLine = (props) => {
    console.log(window.location.href);

    const linkNames = {
        payment: 'Оформление заказа',
        about: 'О магазине',
        news: 'Новости',
        home: 'Главная',
        contacts: 'Контакты'
    }
    const currentUrl = document.location.href;
    const currentUrlList = currentUrl.split("/");
    const listArr = []

    for (let i = 2 ; i <= currentUrlList.length - 1 ; i++){
        listArr.push(currentUrlList[i])
    }
 
    const crumbsList = listArr.map(el => { return <BreadcrumbItem key={el} name={linkNames[el]} link={el}/>} )
  
    return(
        <Div {...props}>
            
            <Breadcrumb separator="/">
            <Breadcrumb.Item> Главная </Breadcrumb.Item>
                { crumbsList }
            </Breadcrumb>
        </Div>
    )
}

export default BreadcrumbLine
