import React from 'react'
import styled from 'styled-components'
import FormContainer from './Form/FormContainer'

const Div = styled.div`
    flex: 0 0 350px;
    background: #F9F9F9;
    margin-right: 22px;
`

const Filters = (props) => {
    return(
        <Div>
            <FormContainer/>
        </Div>
    )
}

export default Filters
