import { CloseOutlined } from '@ant-design/icons'
import React from 'react'
import styled from 'styled-components'

const Div = styled.div`
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    margin-bottom: 50px;
    @media(max-width: 991px){
        flex-wrap: wrap
    }
    :after{
        content: '';
        position: absolute;
        display: block;
        width: 100%;
        height: 1px;
        background: #000;
        bottom: -25px;
    }
`
const Img = styled.div`
    max-width: 130px;
    margin-right: 5px;
    @media(max-width: 991px){
        display: flex;
        justify-content: center;
        margin: 0 auto 25px;
        max-width: 60%;
    }
    @media(max-width: 525px){
        margin-bottom: 10px;
    }
    img{
        display: block;
        width: 100%;
    }
`
const Title = styled.div`
    max-width: 170px;
    margin-right: 2px;
    @media(max-width: 1198px){
        max-width: 120px;
    }
    @media(max-width: 991px){
        max-width: 170px;
        margin-right: 15px;
    }
    @media(max-width: 525px){
        flex: 1 1 100%;
        max-width: none;
        margin-bottom: 10px;
    }
`
const Label = styled.label`
    align-self: center;
    @media(max-width: 991px){
        margin-right: 15px;
    }
`
const Input = styled.input`
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 14px;
    color: #333333;

    border-radius: 3px;
    padding: 5px 4px;

    width: 50px;
    appearance: none;
    margin: 0 5px 0 0;
`
const Cost = styled.div`
    align-self: center;
    display: flex;
    align-content: center;
    line-height: 31px;
    margin-right: 5px;

    color: #333333;
    @media(max-width: 991px){
        margin-right: 15px;
    }

`
const Button = styled.button`
    background: none;
    border: 0;
`
const Action = styled.div`
    display: flex;
    justify-content: space-between;
    flex: 0 0 70%;
    flex-wrap: wrap;
    @media(max-width: 991px){
        flex: 1 1 100%;
        justify-content: flex-end;
    }
    @media(max-width: 525px){
        justify-content: space-between;
    }
`


const BasketItem = (props) => {
    const changeTotalCost = (e) => {
        const amount = e.target.value
        const cost = props.item.currentCost.replace(/\s/g, '')
        props.setCost(cost * amount)
    }
    const getTotalCost = (cost, amount) => {
        const totalCost = cost * amount
        return totalCost
    }
    console.log(getTotalCost);
    return (
        <Div>
            <Img>
                <img src={props.item.image} alt="#" />
            </Img>
            
            <Action>
            <Title>{props.item.title}</Title>
                <Label>
                    <Input onChange={changeTotalCost} value={props.amout} type="number" />
                    шт.
                </Label>
                <Cost>{getTotalCost(props.item.currentCost.replace(/\s/g, ''), props.amout)} грн.</Cost>
                <Button>
                    <CloseOutlined />
                </Button>
            </Action>
            
        </Div>
    )
}

export default BasketItem