import React from 'react'
import styled from 'styled-components'

const Form = styled.form`
    display: flex;
    margin-bottom: 30px;
`
const Title = styled.div`
    font-style: normal;
    font-weight: bold;
    font-size: 14px;
    line-height: 16px;

    color: #333333;
    margin-right: 26px;
    :not(:first-child){
        margin-left: 60px;
    }
`
const Label = styled.label`
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 16px;
    border-bottom: 1px solid #333333;

    color: #333333;
    margin-right: 40px;
    display: flex;
    align-items: center;
    padding-right: 20px;
    position: relative;
`
const Span = styled.span`
    display: block;
    width: 10px;
    height: 10px;

    position: absolute;
    right: 0;
    top: 4px;

    width: 6px;
    height: 6px;
    border-top: 1px solid #333333;
    border-right: 1px solid #333333;



    transform: rotate(135deg) translateX(0);

`
const Input = styled.input`
    position: absolute;
    width: 1px;
    height: 1px;
    left: 0;
    top: 0;
    clip: rect(0 0 0 0);
`


const Sort = (props) => {
    return(
        <Form>
            <Title>Сортировать по:</Title>
            <Label>
                <Input type="checkbox" name='cost'/>
                цене
                <Span></Span>
            </Label>
            <Label>
                <Input type="checkbox" name='popular'/>
                популярности
                <Span></Span>
            </Label>
            <Label>
                <Input type="checkbox" name='reviews '/>
                отзывам
                <Span></Span>
            </Label>
            <Title>Показывать только:</Title>
            <Label>
                <Input type="checkbox" name='discont'/>
                со скидкой
                <Span></Span>
            </Label>

        </Form>
    )
}

export default Sort
