import React from 'react'
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import ItemSlider from './ItemSlider.jsx/ItemSlider';
import styled from 'styled-components'

const Div = styled.div`
    
    .slick-slide:not(.slick-current){
      transform: scale(.5);
      span {
        display: none;
      }
    }
    .slick-slide{
      transition: .5s linear;
    }
`

const TopSalesSlider = (props) => {

    let settings = {
        speed: 300,
        infinite: true,
        autoplay: true,
        slidesToShow: 3,
        centerMode: true,
        slidesToScroll: 1,
        responsive: [
          {
            breakpoint: 991,
            settings: {
              slidesToShow: 2,
              centerMode: false,
            }
          },
          {
            breakpoint: 789,
            settings: {
              slidesToShow: 1,
              centerMode: false,
            }
          }
        ]
      };
    
      const ItemsList = props.topSales.map(el => {
        return <ItemSlider 
            key={el.id}
            image = {el.image}
            title = {el.title}
            oldCost = {el.oldCost}
            currentCost = {el.currentCost}
            labels = {el.labels} 
          />
      })
    return(
        <Div>
            <Slider {...settings}>
              { ItemsList }
            </Slider>

        </Div>
    )
}
export default TopSalesSlider
