import React from 'react'
import styled from 'styled-components'
import Label from './Label/Label'

const Div = styled.div`
    width: 100%;
    display: flex;
    flex-direction: column;
    position: relative;
    
`
const Img = styled.div`
    width: 100%;
    height: 200px;
    margin-bottom: 40px;
    position: relative;
    img{
        position: absolute;
        height: 100%;
        width: 100%;
        object-fit: contain;
    }
`
const Row = styled.span`
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
`
const Title = styled.div`
    font-style: normal;
    font-weight: bold;
    font-size: 14px;
    line-height: 143%;
    color: #333333;
    flex: 0 1 180px;
    text-align:justify;
    @media(max-width: 1365px){
        flex: 1 1 100%;
        margin-bottom: 10px;
    }
`
const Cost = styled.div`
    display: flex;
    flex-direction: column;
`
const OldCost = styled.div`
    font-style: normal;
    font-weight: bold;
    font-size: 14px;
    line-height: 16px;
    text-decoration-line: line-through;

    color: #888888;
`
const CurrentCost = styled.div`
    font-style: normal;
    font-weight: bold;
    font-size: 20px;
    line-height: 23px;

    color: #EF4155;

`
const Button = styled.button`
    display: flex;
    justify-content: center;
    align-items: center;
    width: 150px;
    height: 40px;

    font-style: normal;
    font-weight: bold;
    font-size: 18px;
    line-height: 21px;
    color: #333333;
    text-transform: uppercase;

    background: #FFCC01;
    border-radius: 5px;
    border: 0;
`
const Labels = styled.span`
    display: block;
    position: absolute;
    top: 0;
    right: 0;
`

const ItemSlider = (props) => {

    const labelsList = props.labels.map( el => {
        return <Label text={el}/>
    })
    return (
        <Div className={'HELLLo'}>
            <Img>
                <img src={props.image} alt="Топ продаж" />
            </Img>
            <Row>
                <Title>{props.title}</Title>
                <Cost>
                    <OldCost> {props.oldCost ? props.oldCost : null}</OldCost>
                    <CurrentCost> {props.currentCost} </CurrentCost>
                </Cost>
                <Button> купить </Button>
            </Row>
            <Labels>
                { labelsList }
            </Labels>
        </Div>
    )
}
export default ItemSlider