const ADD_NEW_PROCUCT = 'ADD-NEW-PROCUCT'
const CHANGE_FULLNAME = 'CHANGE-FULLNAME'
const CHANGE_PHONE = 'CHANGE-PHONE'
const CHANGE_EMAIL = 'CHANGE-EMAIL'

const initialState = {
    basket: [
        {id: 1, amount: 1},
        {id: 2, amount: 2},

    ],
    form: {
        fullName: '',
        phone: '',
        email: ''
    }
}

const paymentReducer = (state = initialState, action = {}) => {
    switch (action.type) {
        case ADD_NEW_PROCUCT:
            if (action.productId) {
                return {
                    ...state,
                    basket: {
                        ...state.basket,
                        ...state.basket.name = 1
                    }
                }
            } else {
                return {
                    ...state,
                    basket: {

                    }
                }
            }
            case CHANGE_FULLNAME:
                return {
                    ...state,
                    ...state.form.fullName = action.fullName
                }
                case CHANGE_PHONE:
                    return {
                        ...state,
                        ...state.form.phone = action.phone
                    }
                    case CHANGE_EMAIL:
                        return {
                            ...state,
                            ...state.form.email = action.email
                        }
                        default:{
                            return state
                        }
    }
}

export const addNewProductActionCreator = (productId) => {
    return {
        type: ADD_NEW_PROCUCT,
        productId: productId
    }
}
export const changePhoneActionCreator = (fullName) => {
    return {
        type: CHANGE_FULLNAME,
        fullName: fullName
    }
}
export const changeFullNameActionCreator = (phone) => {
    return {
        type: CHANGE_PHONE,
        phone: phone
    }
}
export const changeEmailActionCreator = (email) => {
    return {
        type: CHANGE_EMAIL,
        email: email
    }
}




export default paymentReducer


