import React from 'react'
import styled from 'styled-components'
import Info from './Info/Info'
import Order from './Order/Order'


const Div = styled.div`
    display: flex;
    flex-direction: column;
    margin-right: 40px;
`
const About = (props) => {
    return(
        <Div>
            <Order/>
            <Info/>
        </Div>
    )
}

export default About
