import React from 'react'
import styled from "styled-components";

const Button = styled.button`
    font-style: normal;
    font-weight: bold;
    font-size: 18px;
    line-height: 21px;
    width: 160px;
    height: 50px;
    background: #FFCC01;
    text-transform: uppercase;
    border-radius: 5px;
    
    display: flex;
    justify-content: center;
    align-items: center;

    color: #333333;
    margin-bottom: 29px;
`
const ProductAddButton = (props) => {

    const addProductToCart = (productId) => {
        props.addNewProductActionCreator(productId)
    }
    return(
        <Button onClick={ ()=> {addProductToCart(props.id)} }>купить</Button>
    )
}

export default ProductAddButton
