import React from 'react'
import styled from 'styled-components'
import Content from './Content/Content'
import Filters from './Filters/Filters'

const Div = styled.div`
    display: flex;
`

const Body = (props) => {
    return(
        <Div>
            <Filters/>
            <Content/>
        </Div>
    )
}

export default Body
