import React from 'react'
import styled from "styled-components";
import Body from './Body/Body';

const Div = styled.div`
    padding: 0 15px;
    flex: 1 1 33%;
    display: flex;
`

const Product = (props) => {

    return(
        <Div>
           <Body {...props}/>
        </Div>
    )
}

export default Product
