import React from 'react'
import styled from 'styled-components'
import Rating from '../../../../../global/Products/Product/Body/Item/Rating/Rating'


const Div = styled.div`
    display: flex;
    flex-direction: column;
    margin-right: 40px;
`
const Row = styled.div`
    font-style: normal;
    font-weight: bold;
    font-size: 14px;
    line-height: 16px;

    color: #333333;

    display: flex;
    justify-content: space-between;
    margin-bottom: 23px;
`
const View = styled.div`
    background: #F9F9F9;
    padding: 17px 31px;
    margin-bottom: 12px;
`


const Order = (props) => {
    return(
        <Div>
            <Row>
                <div> {props.isStock ? 'В наличии' : 'Временно отсутствует'} </div>
                <div>
                    <Rating/>
                </div>
            </Row>
            <View>
                {/* <Colors/> */}
                {/* <FrameSizes/> */}
            </View>
            {/* <Order/> */}
            {/* <CharacteristicsContainer/> */}
        </Div>
    )
}

export default Order


















