import React, { useState } from 'react'
import { Modal } from 'antd';
import styled from "styled-components";
import CallbackRequest from '../../../../../popup/CallbackRequest/CallbackRequest';



const ButtonPopup = styled.button`
    display: block;
    width: 100%;
    text-align: center;
    font-style: normal;
    font-weight: 300;
    font-size: 14px;
    line-height: 16px;
    background: none;
    border: 0;
    text-decoration-line: underline;
    color: #0BB812;
   
    @media(max-width: 769px){
        display: none;
    }
`

const Callback = () => {
    const [isPopupOpen, setIsPopupOpen] = useState(false);
    const showModal = () => {
        setIsPopupOpen(true);
    };
    const handleOk = () => {
        setIsPopupOpen(false);
    };
    
    const handleCancel = () => {
        setIsPopupOpen(false);
    };
    return (
        <div>
            <ButtonPopup type="button" onClick={showModal}> Заказать обратный звонок </ButtonPopup>
            <Modal bodyStyle={{padding: '0px'}} width={460} footer={null} visible={isPopupOpen} onOk={handleOk} onCancel={handleCancel}>
                <CallbackRequest/>
            </Modal>
        </div>
    )
}

export default Callback